import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCartArrowDown,
  faCartPlus,
  faCartShopping,
  faBasketShopping,
  faBagShopping,
} from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
require("./index.css");
const StickyIcon = () => {
  const [title, setTitle] = useState(document.title);

  useEffect(() => {
    // Function to change the title when the tab is hidden
    const changeTitleOnHidden = () => {
      document.title = document.hidden ? "Tab is Hidden" : title;
    };

    // Event listener for visibility change
    document.addEventListener("visibilitychange", changeTitleOnHidden);

    // Clean up the effect to avoid side effects
    return () => {
      document.removeEventListener("visibilitychange", changeTitleOnHidden);
      document.title = title; // Set a default title on component unmount
    };
  }, []); // Runs only on mount

  const handleTitleChange = () => {
    // Update the title when a button is clicked
    setTitle("New Title");
  };

  return (
    <div>
      <h1>{title}</h1>
      <button onClick={handleTitleChange}>Change Title</button>
    </div>
  );
};

export default StickyIcon;
